@media all and (min-width: 1600px) {
  .memo {
    width: 29%;
  }
}
@media all and (max-width: 1600px) and (min-width: 1440px) {
  .memo {
    width: 45%;
  }
}
@media all and (min-width: 1280px) and (max-width: 1440px) {
  .memo {
    width: 43%;
  }
}
@media all and (min-width: 1024px) and (max-width: 1280px) {
  .memo {
    width: 43%;
  }
}
@media all and (min-width: 768px) and (max-width: 1024px) {
  .memo {
    width: 43%;
  }
}
@media all and (max-width: 768px) {
  .memo {
    width: 100%;
  }
}
.memo {
  position: relative;
  overflow: hidden;
  background: #fff;
  margin: 10px;
  padding: 5px 15px 28px;
  border: 1px solid #eaebec;
  transition: all .2s linear;
  line-height: 1.8;
}
.memo:hover {
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  transform: translate3d(0, -5px, 0);
}
.memo:hover .memo-date {
  cursor: default;
  font-size: 14px;
  color: #ACE4DF;
}
.memo .memo-date {
  position: absolute;
  bottom: 2px;
  right: 5px;
  font-size: 12px;
  color: #f6f6f6;
  transition: color, font-size 0.3s, 0.3s ease-in-out;
}



